var IMPORT_REGEX = /^import\s/;
var EXPORT_REGEX = /^export\s/;
var EXPORT_DEFAULT_REGEX = /^export default\s/;
var STARTS_WITH_CAPITAL_LETTER_REGEX = /^[A-Z]/;
var EMPTY_NEWLINE = '\n\n';
var COMMENT_OPEN = '<!--';
var COMMENT_CLOSE = '-->';

var isImport = function isImport(text) {
  return IMPORT_REGEX.test(text);
};

var isExport = function isExport(text) {
  return EXPORT_REGEX.test(text);
};

var isExportDefault = function isExportDefault(text) {
  return EXPORT_DEFAULT_REGEX.test(text);
};

var isImportOrExport = function isImportOrExport(text) {
  return isImport(text) || isExport(text);
};

var isComment = function isComment(str) {
  return str.startsWith(COMMENT_OPEN) && str.endsWith(COMMENT_CLOSE);
};

var getCommentContents = function getCommentContents(str) {
  return str.slice(COMMENT_OPEN.length, -COMMENT_CLOSE.length);
};

var startsWithCapitalLetter = function startsWithCapitalLetter(str) {
  return STARTS_WITH_CAPITAL_LETTER_REGEX.test(str);
};

var paramCase = function paramCase(string) {
  return string.replace(/([a-z0-9])([A-Z])/g, '$1-$2').replace(/([a-z])([0-9])/g, '$1-$2').toLowerCase();
};

var toTemplateLiteral = function toTemplateLiteral(text) {
  var escaped = text.replace(/\\(?!\$)/g, '\\\\') // Escape all "\" to avoid unwanted escaping in text nodes
  // and ignore "\$" since it's already escaped and is common
  // with prettier https://github.com/mdx-js/mdx/issues/606
  .replace(/`/g, '\\`') // Escape "`"" since
  .replace(/(\\\$)/g, '\\$1') // Escape \$ so render it as it is
  .replace(/(\\\$)(\{)/g, '\\$1\\$2') // Escape \${} so render it as it is
  .replace(/\$\{/g, '\\${'); // Escape ${} in text so that it doesn't eval

  return '{`' + escaped + '`}';
};

module.exports.EMPTY_NEWLINE = EMPTY_NEWLINE;
module.exports.isImport = isImport;
module.exports.isExport = isExport;
module.exports.isExportDefault = isExportDefault;
module.exports.isImportOrExport = isImportOrExport;
module.exports.startsWithCapitalLetter = startsWithCapitalLetter;
module.exports.isComment = isComment;
module.exports.getCommentContents = getCommentContents;
module.exports.paramCase = paramCase;
module.exports.toTemplateLiteral = toTemplateLiteral;